'use client';

import { SandpackProvider, SandpackLayout, SandpackPreview } from '@codesandbox/sandpack-react';
import { memo } from 'react';

interface CustomCodeProps {
  html: string;
  js: string;
  dependencies?: Record<string, string>;
}

export const CustomCode = memo(
  ({ html = '', js = '', dependencies = {} }: CustomCodeProps) => {
    if (!html || !js) {
      return null;
    }

    return (
      <SandpackProvider
        template="vanilla"
        customSetup={{
          entry: 'index.html',
          dependencies,
        }}
        files={{
          '/src/index.js': js,
          '/index.html': html,
        }}
      >
        <SandpackLayout className="border !border-gray-100 dark:!border-gray-300/10 overflow-hidden">
          <SandpackPreview
            showOpenInCodeSandbox={process.env.NODE_ENV === 'development'}
            showRefreshButton={process.env.NODE_ENV === 'development'}
            className="[&>*]:!block !h-full !bg-white dark:!bg-black"
          />
        </SandpackLayout>
      </SandpackProvider>
    );
  },
  () => true
);

CustomCode.displayName = 'CustomCode';
